.rl_scorecard{width: 100%;background: rgb(255, 255, 255) none repeat scroll 0% 0%;margin-top: 30px;}
.rl_scorecard h4{text-align: center;margin-bottom: 0px;line-height: 30px;background-color: #ededed;font-size:14px;}
.rl_scorecard h3{text-align: center;margin-top: 0px;font-size: 42px;color: rgb(120, 0, 0);}*/
.rest_info_column_one h1 {
  margin-bottom: 3px;
  padding-top: 10px;
}
.rest_info_column_one p {
  margin-bottom: 0px;
  font-family: roboto-regular;
}
.restaurants_categories {
  color: #224104;
  font-weight: bold;
}
.rest_address_info {
  background-color: #ffffff;
}
.rest_address_info p {
  padding: 0px 15px;
}
.restaurant_quicklinks ul {
  margin-top: 20px;
  margin-bottom: 15px;
  height: 25px;
}
.restaurant_quicklinks ul li {
  float: left;
}
.restaurant_quicklinks ul li a {
  color: #ffffff;
  padding: 10px 10px;
  margin-right: 10px;
  border-radius: 3px;
  background-color: #b60b04;
  line-height: 30px;
}
.restaurant_quicklinks ul li a:hover {
  text-decoration: none;
  background-color: #b94a48;
}
.restaurant_quicklinks ul li p {
  margin-top: 7px;
  font-style: italic;
  font-size: 12px;
}
.rest_address_info p {
  margin: 0px 0px;
}
.rest_review_box {
  border-bottom: 3px solid #FFE1E1;
  padding-bottom: 15px;
  padding-top: 15px;
  display: inline-block;
}
.rest_review_section {
  display: table-cell;
}
.rest_review_section h2 {
  font-family: trocchi-regular;
  margin-left: 15px;
  color: #444444;
}
.rest_detailed_info_section {
  background-color: #fbfbfb;
  height: 100%;
  display: table-cell;
  padding: 30px 20px;
}
.review_detailed_info_section {
  width: 100%;
  background-color: #ededed;
  height: auto;
}
.rest_review_user_info p {
  text-align: center;
  color: #646464;
  font-size: 0.9em;
  font-family: roboto-regular;
}
.rest_review_user_image {
  width: 130px;
  margin: 5px auto;
}
.review_author {
  color: #B60B04;
  font-weight: bold;
}
.review_author_highlight {
  font-weight: bold;
  color: #b60b04;
}
.full_review_text p {
  font-size: 10pt;
  font-family: lato-regular;
  line-height: 20px;
}
.review_quicklinks {
  text-align: right;
}
.review_quicklinks p {
  font-size: 9pt;
  font-style: italic;
  font-weight: bold;
  color: #cb1818;
}
.review_quicklinks ul li {
  float: left;
}
.review_quicklinks ul li a {
  padding: 8px 20px;
  border-radius: 3px;
  margin: 5px 10px;
  background-color: #3F7E00;
  color: #ffffff;
}
.open_close_box,
.specialities_box,
.other_info_box,
.also_viewed_box {
  margin-bottom: 20px;
  padding-bottom: 15px;
  border-bottom: 3px solid #ededed;
}
.also_visited_rest {
  width: 100%;
  height: 100%;
  display: inline-block;
}
.also_visited_rest_image {
  width: 30%;
}
.also_visited_rest_info {
  width: 69%;
}
.also_visited_rest_info p {
  margin-left: 10px;
  font-size: 10pt;
}
.col-md-3 {
  width: 21%;
  float: left;
}
.col-md-4 {
  width: 33%;
  float: left;
}
.col-md-6 {
  width: 50%;
  float: left;
}
.col-md-8 {
  width: 66%;
  float: left;
}
.col-md-9 {
  width: 75%;
  float: left;
}
